h3,
h4,
h5,
h6,
p,
.fake-h4,
.fake-h3,
.flatlist__item__container__landlord__nam,
.flatlist__item__header {
  margin: 0;
}

ul,
ol,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

html {
  overflow-x: hidden;
}

html.is-fixed {
  overflow: hidden;
  touch-action: none;
}

html.is-fixed body {
  overflow: hidden;
}

body {
  min-width: 320px;
  width: 100%;
  height: 100%;
  position: relative;
  color: #000;
  font-family: hnc-300, sans-serif;
  font-size: 16px;
  line-height: 1.5;
  background-color: #000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-overflow-scrolling: touch;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body.is-fixed, body.fancybox-enabled {
  overflow: hidden;
}

body.loading .preloader {
  display: block;
}

body.modal-open {
  padding-right: 0 !important;
}

header::after,
footer::after,
main::after,
div::after,
section::after,
ul::after,
ol::after {
  display: block;
  content: "";
  clear: both;
}

iframe {
  border: 0;
}

select {
  width: 100%;
  outline: none;
  background-color: transparent;
}

select option[disabled] {
  color: #9b9b9b;
}

textarea {
  width: 100%;
  padding: 10px 15px;
  background-color: transparent;
  resize: none;
}

b,
strong {
  font-weight: 600;
}

a {
  color: #ffd30f;
  text-decoration: none;
  outline: none;
  transition: all 0.35s ease;
}

a:hover {
  color: #ffdc42;
}

a.is-disable {
  pointer-events: none;
}

h1, .h1, .page__title, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
  margin-top: 0;
}

h1, .h1, .page__title {
  font-family: hnc-500, sans-serif;
  font-weight: 500;
  font-size: 35px;
  line-height: 1.5;
  text-transform: uppercase;
  letter-spacing: 2px;
}

@media (max-width: 480px) {
  h1, .h1, .page__title {
    font-size: 22px;
  }
}

h2, .h2 {
  font-weight: 600;
  font-size: 36px;
  line-height: 1.4em;
}

@media screen and (max-width: 1024px) {
  h2, .h2 {
    font-size: 32px;
    line-height: 1.25em;
  }
}

@media (max-width: 768px) {
  h2, .h2 {
    font-size: 26px;
    line-height: 1.2em;
  }
}

h3, .h3 {
  font-weight: 500;
  font-size: 28px;
  line-height: 1.25em;
}

@media screen and (max-width: 1024px) {
  h3, .h3 {
    font-size: 24px;
  }
}

@media (max-width: 768px) {
  h3, .h3 {
    font-size: 22px;
    line-height: 1.13636364em;
  }
}

h4, .h4, h5, .h5, h6, .h6 {
  font-size: 18px;
  font-weight: 300;
  line-height: 1.22222222em;
  margin-bottom: 10px;
}

@media (max-width: 768px) {
  h4, .h4, h5, .h5, h6, .h6 {
    line-height: 1.11111111em;
  }
}

input, textarea, select {
  outline: none;
  appearance: none;
  -webkit-appearance: none;
  box-shadow: none;
}

textarea {
  width: 100%;
  min-height: 100px;
  padding: 4px 10px;
  font-size: 14px;
  line-height: 1.25;
}

@media (max-width: 480px) {
  textarea {
    font-size: 16px;
  }
}

textarea:focus {
  font-size: 14px;
}

textarea:-ms-input-placeholder {
  line-height: 1.25;
}

textarea:-moz-placeholder {
  line-height: 1.25;
}

textarea::-moz-placeholder {
  line-height: 1.25;
}

textarea::-webkit-input-placeholder {
  line-height: 1.25;
}

textarea::placeholder {
  line-height: 1.25;
}

input[type='num'],
input[type='number'],
input[type='tel'],
input[type='time'],
input[type='date'] {
  appearance: none;
}

input[type='num']::-webkit-outer-spin-button, input[type='num']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button,
input[type='number']::-webkit-inner-spin-button,
input[type='tel']::-webkit-outer-spin-button,
input[type='tel']::-webkit-inner-spin-button,
input[type='time']::-webkit-outer-spin-button,
input[type='time']::-webkit-inner-spin-button,
input[type='date']::-webkit-outer-spin-button,
input[type='date']::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/*disable auto-zoom on iphone input field focus*/
@media screen and (-webkit-min-device-pixel-ratio: 0) and (max-width: 480px) {
  select:focus,
  textarea:focus,
  input:focus {
    font-size: 16px;
  }
}

.text-color--red {
  color: red;
}

.text-color--pink {
  color: #fe0061;
}

.text-color--purple {
  color: #ab00b6;
}

.text-color--blue {
  color: #3c4ebc;
}

.text-color--blue--light {
  color: #0096fb;
}

.text-color--blue--lightest {
  color: #00bfd8;
}

.text-color--green--dark {
