  margin: 17px 0 15px;
}

.section {
  padding: 70px 0 80px;
  background-color: #fff;
}

@media (max-width: 480px) {
  .section {
    padding: 40px 0 50px;
  }
}

.section__title {
  max-width: 780px;
  margin-bottom: 30px;
  font-family: hnc-500, sans-serif;
  font-weight: 500;
  font-size: 38px;
  line-height: 1.3;
}

@media (max-width: 1024px) {
  .section__title {
    font-size: 30px;
  }
}

@media (max-width: 480px) {
  .section__title {
    max-width: 100%;
    margin-bottom: 15px;
    font-size: 22px;
  }
}

.section__desc {
  margin-bottom: 10px;
  font-size: 20px;
  font-weight: 300;
}

@media (max-width: 480px) {
  .section__desc {
    font-size: 16px;
  }
}

.section__desc:last-child,
.section__desc .mb-0 {
  margin-bottom: 0;
}

.section__desc li {
  font-size: 18px;
}

@media (max-width: 480px) {
  .section__desc li {
    font-size: 16px;
  }
}

.section__desc li + li {
  margin-top: 6px;
}

.section__desc.mb-0 {
  margin-bottom: 0;
}

.section__desc.w80 {
  width: 80%;
}

@media (max-width: 768px) {
  .section__desc.w80 {
    width: 100%;
  }
}

.section--img {
  min-height: 800px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

@media (max-width: 1024px) {
  .section--img {
    min-height: 536px;
  }
}

.section.bg--gray {
  background-color: #f3f3f3;
}

.section .list {
  margin-top: 15px;
}

.page-controls span {
  font-size: 18px;
  font-weight: 300;
  padding: 0 0.5em;
  padding-top: 0;
  transition: opacity 0.3s ease 0s;
}

.page-controls__arrow {
  display: flex;
  max-width: 15em;
  height: 4em;
  color: #fff;
  background-color: #000;
  border: 0;
  border-radius: 0;
  outline: none;
  position: fixed;
  top: 50%;
  z-index: 99;
  transform: translateY(-50%);
  transition: transform 0.3s ease-out 0s, left 0.3s ease-out 0s, right 0.3s ease-out 0s;
}

.page-controls__arrow--prev {
  text-align: right;
  left: 0;
  transform: translateX(-100%);
}

.page-controls__arrow--prev::after {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 2em 0 2em 2em;
  border-color: transparent transparent transparent #000;
  transition: all 0.3s ease;
  left: 100%;
  top: 50%;
  margin-top: -2em;
  margin-left: -1px;
}

.page-controls__arrow--prev .page-controls__ico {
  right: -15px;
}

.page-controls__arrow--prev span {
  padding-right: 2em;
}

.page-controls__arrow--next {
  text-align: left;
  right: 0;
  transform: translateX(100%);
}

.page-controls__arrow--next::after {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 2em 2em 2em 0;
  border-color: transparent #000 transparent transparent;
  transition: all 0.3s ease;
  right: 100%;
  top: 50%;
  margin-top: -2em;
}

.page-controls__arrow--next .page-controls__ico {
  left: -15px;
}

.page-controls__arrow--next span {
  padding-left: 2em;
}

.page-controls__ico {
  font-size: 25px;
  line-height: 1;
  transition: left 0.3s ease-out 0s, right 0.3s ease-out 0s;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
}

.firstscreen {
  height: 100vh;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  position: relative;
  display: flex;
}

.firstscreen::before {
  display: block;
  content: '';
  background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.45) 0%, rgba(0, 0, 0, 0) 100%);
  transition: all 1s ease;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.firstscreen > * {
  z-index: 1;
}

.firstscreen .container {
  position: relative;
}

@media (max-width: 480px) {
  .firstscreen {
    background-position: 35% 50%;
  }
}

.firstscreen__icon {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 30px;
}

.firstscreen__icon .icon {
  width: 30px;
  height: 30px;
  fill: #fff;
  transform: rotate(90deg);
}

.firstscreen > .container {
  padding-top: 170px;
  backface-visibility: hidden;
}

@media (max-width: 480px) {
  .firstscreen > .container {
    padding-top: 100px;
  }
}

.firstscreen.scale-out {
  transform: scale(0.9);
}

.list__title {
  margin-bottom: 10px;
  font-weight: 300;
  font-size: 20px;
}

@media (max-width: 480px) {
  .list__title {
    font-size: 16px;
  }
}

.list li {
  font-weight: 300;
  padding-left: 15px;
  position: relative;
}

.list li::before {
  content: '';
  display: block;
  width: 4px;
  height: 4px;
  background-color: #c4c2ce;
  position: absolute;
  top: 11px;
  left: 0;
}

.video {
  width: 80%;
  padding-bottom: 57.25%;
  margin: auto;
  position: relative;
  cursor: pointer;
}

.video__btn {
  cursor: pointer;
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.video__btn img {
  max-width: 280px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-51%, -50%);
}

@media (max-width: 768px) {
  .video__btn img {
    max-width: 180px;
  }
}

@media (max-width: 480px) {
  .video__btn img {
    max-width: 80px;
  }
}

.video video {
  margin: 0;
  border: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.swiper-container {
  width: 100%;
  height: 34vw;
  transition: opacity 0.6s ease, transform 0.3s ease;
}

@media (min-width: 479px) {
  .swiper-container {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 11;
  }
}

@media (max-width: 768px) {
  .swiper-container {
    height: auto;
  }
}

.swiper-container:hover .swiper-button-prev,
.swiper-container:hover .swiper-button-next {
  transform: translateX(0);
  opacity: 1;
  visibility: visible;
}

.swiper-container.loading {
  opacity: 0;
}

.swiper-container.scale-out {
  transform: scale(0.96);
}

.swiper-container.scale-in {
  transform: scale(1);
}

.swiper-container-cube .swiper-slide {
  height: 100vh;
}

@media (max-width: 480px) {
  .swiper-container-3d .swiper-wrapper {
    transform: scale(1);
  }
}

.swiper-slide {
  background-position: center;
  background-size: cover;
  perspective: 1600px;
}

.swiper-slide.swiper-slide-active {
  z-index: 2;
}

.swiper-slide.swiper-slide-active .firstscreen {
  transition: transform 1s ease;
}

@media (min-width: 1023px) {
  .swiper-slide.swiper-slide-active .firstscreen.scale-out {
    transform: scale(0.95);
  }
}

.swiper-slide .entity-img {
  display: none;
}

.swiper-slide .content {
  position: absolute;
  top: 40%;
  left: 0;
  width: 50%;
  padding-left: 5%;
  color: #fff;
  transform: translateZ(100px) translateX(100px);
}

.swiper-slide .content .title {
  font-size: 2.5em;
  font-weight: bold;
  margin-bottom: 30px;
}

.swiper-slide .content .caption {
  display: block;
  font-size: 12px;
  line-height: 1.4;
}

[class^='swiper-slide-shadow-'] {
  backface-visibility: hidden;
}

.content {
  position: relative;
  z-index: 11;
}

@media (min-width: 479px) {
  .content {
